"use client"

import TransactionForm from "@/pages/TransactionForm";
import { useRouter } from "next/navigation";
import { useState } from "react";
import  Modal from "@/ui/Modal"; 
import LoadingSpinnerScreen from "@/ui/LoadingSpinnerScreen";
import { ModalProps } from "@/interfaces/IModal";
import { TransactionFormData } from "@/interfaces/ITransaction";
import { createTransaction } from "@/services/transaction";
export default function CreateTransactionPage() {
    const router = useRouter();
    const [isSubmitting, setIsSubmittng] = useState(false)
    const [modal, setModal] = useState<ModalProps | null> (null)

    const handleSubmit = async(form: TransactionFormData) => {
        setIsSubmittng(true)
        try{
            await createTransaction({
                ...form,
                category_id: form.categoryId
            });
            setModal({
                type: "success",
                message: "trasaksi berhasil ditambahkan"
            })
        }catch (error) {
        if(error instanceof Error) {
            setModal({ message:error.message, type: "danger"})
        }else {
            setModal({message: "terjadi kesalahan", type: "danger"})
        }
        }finally {
            setIsSubmittng(false)
        }
    }
    return (
      <div className="p-6">
        <h1 className="text-2xl font-bold mb-4"> buat trasaksi baru</h1>
        {isSubmitting && <LoadingSpinnerScreen />}
        {modal && (
          <Modal
            type={modal.type}
            message={modal.message}
            onOk={() => {
              setModal(null);
              if (modal.type === "success") {
                router.push("/dashboard/transaction");
              }
            }}
          />
        )}
        <TransactionForm
          initialData={{
            type: "income",
            amount: "40000",
            date: "23-23-2025",
            note: "test",
            categoryId: 1,
          }}
          onSubmit={ handleSubmit}
        />
      </div>
    );
}