import api from "@/api"
import { handleApiError } from "@/utils/handleApiError"
import getTokenHeader from "@/utils/getTokenHeader"


import { CategoryOption } from "@/interfaces/ITransaction";

export const fetchAllCategories = async (): Promise<CategoryOption[]> => {
  try {
    const res = await api.get("/api/v1/category", {
      headers: getTokenHeader(),
    });
    return res.data.data; // ambil langsung array dari response
  } catch (error) {
    console.error("Gagal mengambil kategori:", error);
    return []; // fallback aman
  }
};
export const fetchCategoryById = async(id: number) => {
    try{
        const res = await api.get(`/category/${id}`, {
            headers: getTokenHeader (),
        })
        return res.data;
    } catch (error) {
        handleApiError(error,"failed to fetch category id")
    }
}

export const createCategory = async(data: {name: string; description?:string }) => {
    try{
        const res = await api.post('/category', data, {
            headers: getTokenHeader (),
        })
        return res.data
    } catch (error) {
        handleApiError(error,"failed to create categories")
    }
}

export const updateCategory = async(id:number, data: {  name: string; description?:string }) => {
    try{
        const res = await api.put(`/category/${id}`,data, {
            headers: getTokenHeader (),
        })
        return res.data
    } catch (error) {
        handleApiError(error,"failed to fetch categories")
    }
}

export const deleteCategory = async(id:number) => {
    try{
        const res = await api.delete(`/category/${id}`,{
            headers: getTokenHeader (),
        })
        return res.data
    } catch (error) {
        handleApiError(error,"failed to fetch categories")
    }
}
